import React from "react";
import "./InputAnnotation.css";

export interface InputAnnotationProps {
  text: string;
  isError?: boolean;
}

const InputAnnotation: React.FC<InputAnnotationProps> = ({
  text,
  isError = false,
}) => {
  return (
    <div className={`input-annotation ${isError ? "error" : "info"}`}>
      {text}
    </div>
  );
};

export default InputAnnotation;
